      height: auto;
      min-height: 100px;
      margin: 0 auto;
      box-sizing: border-box;

      div {
        ul.menu {
          list-style: none;
          margin: 0;
          padding: 0;

          li {
            padding: 8px 0;
            transition-duration: 0.5s;
